import React, { Component } from "react";
import {FacebookShareButton, PinterestShareButton, TwitterShareButton} from "react-share";
import "./style.css";

export default class Social extends Component {
  render() {
    const {url, post} = this.props;
    return (
      <div className="social">
        <FacebookShareButton className="social__btn" url={url}>Facebook</FacebookShareButton>
        <TwitterShareButton className="social__btn" url={url} title={post.title}>Twitter</TwitterShareButton>
        <PinterestShareButton className="social__btn" url={url} media={post.img} description={post.title}>Pinterest</PinterestShareButton>
        {/* <a href={"https://www.facebook.com/sharer/sharer.php?u=" + encodeUrl} target="_blank" rel="noopener noreferrer" className="social__btn social__btn--fb">Facebook</a> */}
        {/* <a href={"https://twitter.com/intent/tweet/?url=" + encodeUrl + "?text=" + post.title} target="_blank" rel="noopener noreferrer" className="social__btn social__btn--tw">Twitter</a> */}
        {/* <a href={"http://pinterest.com/pin/create/button/?url=" + encodeUrl + "?description=" + post.text + "?media=" + post.img} target="_blank" rel="noopener noreferrer" className="social__btn social__btn--pin">Pinterest</a> */}
      </div>
    );
  }
}